import React, { useState } from "react";
import CalendarLib from "react-calendar";

import "react-calendar/dist/Calendar.css";
import "./Calendar.scss";

interface Props {
  time: string;
  attendees: string[];
}

const DetailItem = ({ time, attendees }: Props) => {
  return (
    <div className="calendar-detail-item">
      <div className="calendar-detail-item__time">{time}</div>
      <div className="calendar-detail-item__attendees">
        {attendees.map((attendee) => (
          <img src={attendee} alt="" />
        ))}
      </div>
    </div>
  );
};

interface Props2 {
  attendees: string[];
  title: string;
  content: string;
}

const DetailMeeting = ({ attendees, title, content }: Props2) => {
  return (
    <div className="calendar-detail-metting">
      <div className="calendar-detail-metting__attendess">
        {attendees.map((attendee) => (
          <img src={attendee} alt="" />
        ))}
      </div>
      <div className="calendar-detail-metting__title">{title}</div>
      <div className="calendar-detail-metting__content">{content}</div>
    </div>
  );
};

const Calendar = () => {
  const [date, setDate] = useState<Date>(new Date());
  const details = [
    {
      time: "08:30",
      attendees: [
        "data:image/jpeg;base64,",
        "data:image/jpeg;base64,",
      ],
    },
  ];

  return (
    <div className="team-calendar">
      <div className="team-calendar__history">
        <CalendarLib value={date} onChange={setDate} calendarType="US" />
      </div>
      <div className="team-calendar__detail">
        {details.map((detail) => (
          <DetailItem {...detail} />
        ))}
      </div>
      <div className="team-calendar__report">
        <DetailMeeting
          title="  "
          content=" .  .  .  .  .  .  .  .  .  . "
          attendees={details[0].attendees}
        />
      </div>
    </div>
  );
};

export default Calendar;
